
import {defineComponent} from "vue";

export default defineComponent( {
  name: "FramesScaffold",
  props: ['groupId', 'pinned'],
  data() {
    return {
      color: 'grey',
      forceCollapse: null as boolean|null,
      editMode: false,
      newTitle: null,
    }
  },
  computed: {
    finalCollapsed(){
      return this.forceCollapse !== null ? this.forceCollapse : this.pinned.collapsed
    },
    tags(){
      return this.pinned.tags.concat(this.pinned.preProcessedTags)
    },
    finalTitle(){
      if (this.tags.length > 0){
        return this.pinned.title + ' ' + this.tags
      }
      return  this.pinned.title
    }
  },
  methods: {
    openAll(){

    },
    collapse(collapsed: boolean){
      this.forceCollapse = collapsed
    },
    changeColor(){
      let newColor = this.color
      if (this.color === 'grey'){
        newColor = 'blue'
      }
      if (this.color === 'blue'){
        newColor = 'red'
      }
      if (this.color === 'red'){
        newColor = 'yellow'
      }
      if (this.color === 'yellow'){
        newColor = 'green'
      }
      if (this.color === 'green'){
        newColor = 'pink'
      }
      if (this.color === 'pink'){
        newColor = 'purple'
      }
      if (this.color === 'purple'){
        newColor = 'cyan'
      }
      if (this.color === 'cyan'){
        newColor = 'orange'
      }
      if (this.color === 'orange'){
        newColor = 'grey'
      }
      this.color = newColor
    },
    goToViewMode(){
      this.editMode = false
    },
    goToEditMode(){
      this.editMode = true

      this.$nextTick(() => {
        const html = this.$refs.input as HTMLInputElement
        html.focus()
      });

    }
  }
})

